.content_signup_wrapper {
    .content_title {
        text-align: center;
        margin-bottom: 30px;
    }
    .form_wrapper {
        width: 450px;
        margin: 0 auto;
        padding: 10px;
        background-color: rgba(30, 185, 210, 0.44);
        border-radius: 10px;
        .form_title {
            text-align: center;
            font-size: 25px;
        }
        .form_group {
            margin: 10px;
            padding: 5px;
            text-align: center;
            .error_info_wrapper {
                .error_message {
                    background-color: rgb(255, 232, 232);
                    border: 1px solid red;
                    color: red;
                    font-size: 12px;
                    font-weight: bold;
                    padding: 10px 25px;
                    li {
                        list-style-type: disc;
                    }
                }
            }
            .noerror_info_wrapper {
                .noerror_message {
                    background-color: rgb(255, 232, 232);
                    border: 1px solid green;
                    color: green;
                    font-size: 12px;
                    font-weight: bold;
                    padding: 10px 25px;
                    li {
                        list-style-type: disc;
                    }
                }
            }
            label {
                padding-left: 10px;
                text-transform: uppercase;
            }
            input {
                width: 400px;
                height: 40px;
                margin-top: 10px;
                border: none;
                border-radius: 10px;
                padding: 5px 5px 5px 10px;
                outline: none;
                &:focus {
                    border: 2px solid #1fd2a1;
                    outline: none;
                }
            }
            button {
                margin-top: 10px;
                color: white;
                cursor: pointer;
                text-align: center;
                padding: 10px 20px;
                border-radius: 10px;
                width: 100%;
                height: 40px;
                transition: 1s;
                border: none;
                box-shadow: 0 -3px rgb(53, 167, 110) inset;
                transition: 0.2s;
                background-color: #1fd2a1;
                &:hover {
                    background-color: rgb(53, 167, 110);
                }
                &:active {
                    background: rgb(33, 147, 90);
                    box-shadow: 0 3px rgb(33, 147, 90) inset;
                }
            }
        }
    }
    .clear_btn_wrapper {
        button {
            width: 100%;
            color: white;
            cursor: pointer;
            text-align: center;

            border-radius: 10px;
            line-height: 30px;
            height: 30px;
            transition: 1s;
            border: none;
            box-shadow: 0 -3px rgb(53, 167, 110) inset;
            transition: 0.2s;
            background-color: #1fd2a1;
            &:hover {
                background-color: rgb(53, 167, 110);
            }
            &:active {
                background: rgb(33, 147, 90);
                box-shadow: 0 3px rgb(33, 147, 90) inset;
            }
        }
    }
}
